@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300);
.app-header {
  -webkit-align-items: baseline;
          align-items: baseline;
}

.app-header h1 {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  font-size: 6rem;
  color: #999;
  font-family: "Open Sans Condensed", sans-serif;
}

.app-header h2 {
  font-size: 1.2rem;
  color: grey;
}
.btn-group {
  border-radius: .25rem;
  box-shadow: 4px 7px 13px -6px rgba(0,0,0,0.75);
}

.search-input {
  width: auto;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin-right: 3px;
  box-shadow: 4px 7px 13px -6px rgba(0,0,0,0.75);
}
.todo-list-item {
  font-size: 1.25rem;
}

.todo-list-item .circle {
  border-radius: 50%;
  width: 35px;
  margin: 3px 10px 3px 3px;
  border: 1px solid #ddd;
}
.todo-list-item .none {
  color: transparent;
}

.todo-list-item-label {
  margin-left: .3rem;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.todo-list-item.done .todo-list-item-label {
  text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
  font-weight: bold;
  color: steelblue;
}
.todo-list {
  border-radius: .25rem;
  box-shadow: 4px 7px 13px -6px rgba(0,0,0,0.75);
}

.todo-list .list-group-item {
  padding: .25rem .75rem;
}
.item-add-form {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin-top: 10px;
}
.item-add-form input {
  margin-right: 3px;
}
.item-add-form .btn-success {
  box-shadow: 4px 7px 13px -6px rgba(0,0,0,0.75);
}

.clear-btn {
  -webkit-align-self: flex-end;
          align-self: flex-end;
  margin-left: 3px;
  box-shadow: 4px 7px 13px -6px rgba(0,0,0,0.75);
}

.todo-app {
  margin: 1rem auto 0 auto;
  max-width: 500px;
}

.top-panel {
  margin: 1rem 0;
}

.bottom-panel {
  margin: 1rem 0;
}
body {
  background: #eee;
}
